import React from 'react';
import PropTypes from 'prop-types';
import Slider from "react-slick";
import '../../styles/slick.min.css';
import T from '../layout/translation';
import {Col} from 'reactstrap';

export default class YearCarousel extends React.Component {
    state = {
        currentSlide: 0,
    };
    static propTypes = {
        years: PropTypes.array.isRequired,
        clickCallback: PropTypes.func.isRequired
    };
    onClick(year) {
        this.props.clickCallback(year);
        this.setState({ currentSlide: year })
    }
    render() {
        let settings = {
            dots: false,
            infinite: false,
            speed: 500,
            variableWidth: true,
            // responsive: [
            //     {
            //         breakpoint: 600,
            //         settings: {
            //             slidesToShow: 2,
            //             slidesToScroll: 2
            //         }
            //     },
            //     {
            //         breakpoint: 480,
            //         settings: {
            //             slidesToShow: 2,
            //             slidesToScroll: 2
            //         }
            //     }
            // ]
        };
        return(
            <Col  lg={{ size: 4, offset: 4 }}  md={{ size: 6, offset: 3 }} xs="12" className="year-carousel">
                <Slider ref={c => (this.slider = c)} {...settings}>
                    <div key="0" className={this.state.currentSlide === 0 ? 'is-active' : ''} >
                        <button onClick={() => this.onClick(0)}>
                            <T message="All"/>
                        </button>
                    </div>
                    {this.props.years.map((year, key) => (
                        <div key={year} className={this.state.currentSlide === year ? 'is-active' : ''}>
                            <button onClick={() => this.onClick(year)}>{year}</button>
                        </div>
                    ))}
                </Slider>
            </Col>
        )
    }
}