
import _ from 'lodash';
import languages from '../data/global/languages';
export default class DateHandler {
    /**
     * Sort Item By Date And Title
     * @param items
     * @param columns
     * @param sort
     * @returns {Array|*}
     */
    static dateLocalFormat (date, lang){
        let lang_utc = languages.informations[lang].dateUTC;
        let dateFormat  = {year: '' ,yearShort: '', month: '', monthLongString: '', monthShortString: '', day: '', dayLongString: '', dayShortString: ''};
        try{
            let date_object = new Date(date);
            let date_utc = new Date(Date.UTC(date_object.getFullYear(), date_object.getMonth(), date_object.getDay()));
            dateFormat.year = date_utc.toLocaleDateString(lang_utc, {year: "numeric"});
            dateFormat.yearShort = date_utc.toLocaleDateString(lang_utc, {year: "2-digit"});
            dateFormat.month = date_utc.toLocaleDateString(lang_utc, {month: "numeric"});
            dateFormat.monthLongString = date_utc.toLocaleDateString(lang_utc, {month: "long"});
            dateFormat.monthShortString = date_utc.toLocaleDateString(lang_utc, {month: "short"});
            dateFormat.day = date_utc.toLocaleDateString(lang_utc, {day: "numeric"});
            dateFormat.dayLongString = date_utc.toLocaleDateString(lang_utc, {weekday: "long"});
            dateFormat.dayShortString = date_utc.toLocaleDateString(lang_utc, {weekday: "short"});
        }
        catch(e) {console.log(e);}
        return dateFormat;
    };
}